import axios from 'axios';
import React,{useEffect, useRef, useState} from 'react'
import { ApiPath } from '../../App';
import ProductModal from '../../components/ProductModal';
import { Modal } from 'bootstrap';
import DeleteModal from '../../components/DeleteModal';
import Pagination from '../../components/Pagination';


const AdminProducts = () => {
  const [ products, setProducts ] = useState([]);
  const [ pagination, setPagination ] = useState({});
  const [ type,setType ] = useState('create');
  const [ temProduct, setTemProduct ] = useState({});
  const productModal = useRef(null);
  const deleteModal = useRef(null);


  const openProductModal = (type, temProduct)=> {
    productModal.current.show();
    setType(type);
    setTemProduct(temProduct);
  }
  const closeProductModal = ()=> {
    productModal.current.hide();
  };
  const openDeleteProductModal = (temProduct)=> {
    deleteModal.current.show();
    setTemProduct(temProduct);
  };
  const closeDeleteProductModal = ()=> {
    deleteModal.current.hide();
  };
  const deleteProduct = async(id)=> {
    try {
       await axios.delete(`/v2/api/${ApiPath}/admin/product/${id}`);
       closeDeleteProductModal();
       getProjects();
    } catch (error) {
      console.log(error);
    }
  };
  
    useEffect(()=>{
        productModal.current = new Modal('#productModal',{
            backdrop: 'static'
        });
        deleteModal.current = new Modal('#deleteModal', {
            backdrop: 'static'
        })
        getProjects();
    },[]);

    const getProjects = async(page=1)=> {
        const res = await axios.get(`/v2/api/${ApiPath}/admin/products?page=${page}`)
        try {
          const {products} = res.data;
          const {pagination} = res.data;
          setProducts(products);
          setPagination(pagination);
        } catch (error) {
          console.error(error)
        }
    }


  return (
    <div className='p-3'>
      <ProductModal 
        closeProductModal={closeProductModal} 
        getProjects={getProjects}
        type={type}
        temProduct={temProduct}
      />
      <DeleteModal
       temProduct={temProduct}
       closeDeleteProductModal={closeDeleteProductModal}
       deleteProduct={deleteProduct}
      />
      <h3></h3>
      <hr />
      <div className='text-end'>
        <button type='button' className='btn btn-primary btn-sm' onClick={()=> {openProductModal('create',{})}}>
          
        </button>
      </div>
      <table className='table'>
        <thead>
          <tr>
            <th scope='col'></th>
            <th scope='col'></th>
            <th scope='col'></th>
            <th scope='col'></th>
            <th scope='col'></th>
          </tr>
        </thead>
        <tbody>
         {products.map((item, index)=>(
             <tr key={index}>
             <td>{item.category}</td>
             <td>{item.title}</td>
             <td>{item.price}</td>
             <td>{item.is_enabled? "":"" }</td>
             <td>
                 <button 
                  type='button' className='btn btn-primary btn-sm' 
                  onClick={()=> {openProductModal('edit',item)}}>
                 
                 </button>
                 <button
                 type='button'
                 className='btn btn-outline-danger btn-sm ms-2'
                 onClick={(e)=>{
                    openDeleteProductModal(item);
                }}
                 >
                 
                 </button>
             </td>
             </tr>  
          ))}
          
        </tbody>
      </table>
     <Pagination getProjects={getProjects} pagination={pagination}/>  
    </div>
  )
}

export default AdminProducts
