import React, { useEffect, useRef, useState } from 'react'
import { ApiPath } from '../../App';
import axios from 'axios';
import { Modal } from 'bootstrap';
import OrderModal from '../../components/OrderModal';
import Pagination from '../../components/Pagination';


const AdminOrders = () => {
  const [ orders, setOrders ] = useState({});
  const [pagination, setPagination] = useState({});
  const [tempOrder, setTempOrder] = useState({});

  const orderRef = useRef(null);

  const getOrders = async(page=1) => {
    const url = `/v2/api/${ApiPath}/admin/orders?page=${page}` 
    const res = await axios.get(url)
    setOrders(res.data.orders);
    setPagination(res.data.pagination);
  };


  useEffect(()=>{
    getOrders()
  },[]);

   const openOrderModal = (order)=> {
    setTempOrder(order);
    orderRef.current.show();
   };

   const closeOrderModal = ()=> {
    orderRef.current.hide();
   }

   useEffect(()=>{
    orderRef.current = new Modal('#orderModal',{
      backdrop: 'static'
    })
   },[])


  
  return (
    <div className='p-3'>
    <OrderModal
      getOrders={getOrders}
      closeOrderModal={closeOrderModal}
      tempOrder={tempOrder}
    />
    <h3></h3>
    <hr />
    <div className='text-end'>
     
    </div>
    <table className='table'>
      <thead>
        <tr>
          <th scope='col'> id</th>
          <th scope='col'></th>
          <th scope='col'></th>
          <th scope='col'></th>
          <th scope='col'></th>
          <th scope='col'></th>
          <th scope='col'></th>
        </tr>
      </thead>
      <tbody>
        {orders.length > 0 ? (orders.map((order) => {
          return (
            <tr key={order.id}>
              <td>{order.id}</td>
              <td>
                {order.user?.name}
                {order.user?.email}
              </td>
              <td>${order.total}</td>
              <td>
                {order.is_paid ? (
                  <span className='text-success fw-bold'></span>
                ) : (
                  ''
                )}
              </td>
              <td>
                {order.paid_date
                  ? new Date(order.paid_date * 1000).toLocaleString()
                  : ''}
              </td>
              <td>{order.message}</td>

              <td>
                <button
                  type='button'
                  className='btn btn-primary btn-sm'
                  onClick={() => {openOrderModal(order)}}
                >
                  
                </button>
              </td>
            </tr>
          );
        })):(
          <p></p>
        )}
      
      </tbody>
    </table>
    <Pagination pagination={pagination} getProjects={getOrders} />

  </div>
  )
}

export default AdminOrders
